<template>
    <div class="bg-transparent min-h-0 py-3">
        <div class="flex flex-col justify-between h-auto text-white text-18  md:flex-row md:items-center">
            <div class="flex items-center justify-between">
                <a href="" class="text-16 my-3 font-bold sm:text-24">Firmansyah.</a>
                <div class="flex items-center justify-end">
                    <button @click="isOpen = !isOpen" type="button" class="block btn px-2 text-18 md:hidden focus:text-white focus:outline-none">
                        <svg class="h-8 w-8 fill-current" viewBox="0 0 24 24">
                            <transition name="change-all">
                                <path v-if="isOpen" class="" fill-rule="evenodd" d="M18.278 16.864a1 1 0 0 1-1.414 1.414l-4.829-4.828-4.828 4.828a1 1 0 0 1-1.414-1.414l4.828-4.829-4.828-4.828a1 1 0 0 1 1.414-1.414l4.829 4.828 4.828-4.828a1 1 0 1 1 1.414 1.414l-4.828 4.829 4.828 4.828z"/>
                            </transition>
                            <transition name="change-all" >
                                <path v-if="!isOpen" fill-rule="evenodd" d="M4 5h16a1 1 0 0 1 0 2H4a1 1 0 1 1 0-2zm0 6h16a1 1 0 0 1 0 2H4a1 1 0 0 1 0-2zm0 6h16a1 1 0 0 1 0 2H4a1 1 0 0 1 0-2z"/>
                            </transition>
                            
                        </svg>
                    </button>
                    <button class="block btn px-3 bg-gradient-l-success text-10  md:ml-3 shadow-success-smooth hover:shadow-success-strong  sm:px-5 sm:text-14 md:hidden md:text-14">Contact Me</button>
                </div>
            </div>

            <div  :class="!isOpen ? 'hidden': 'block'+'md:block'">
                <div class="nav-group">
                    <a href="">Home</a>
                    <a href="">Services</a>
                    <a href="">About</a>
                    <a href="">Portfolio</a>
                    <button class="hidden btn bg-gradient-l-success  md:ml-3 shadow-success-smooth hover:shadow-success-strong md:block">Contact Me</button>
                </div>
            </div>

        </div>
    </div>
</template>

<script>

export default {
    name: 'Navbar',
    created() {
        window.addEventListener("resize", this.resizeNavbar);
        window.addEventListener("load", this.initNavbar);
    },
    data() {
        return {
            isOpen: false,
            windowWidth: window.innerWidth,
        }
    },
    methods: {
        initNavbar() {
            if ( this.windowWidth > 768 ) {
                this.isOpen = true
            } else {
                this.isOpen = false
            }
        },
        resizeNavbar() {
            this.windowWidth = window.innerWidth;
            if ( this.windowWidth > 768 ) {
                this.isOpen = true
            } else {
                this.isOpen = false
            }
        }
    }
}
</script>

<style lang="scss" scoped>

    .nav-group {

        @apply flex flex-col justify-start;

        @screen md {
            @apply flex-row items-center;
        }

        a {
            @apply text-18 p-2 transition duration-500 rounded-2 my-2 px-2 font-bold;
            &:hover {
                @apply bg-white-300;
            }
            @screen md {
                @apply p-1 px-2;
            }
        }
        & > a:not(:first-child), a:not(:last-child) {
            
            @screen md {
                @apply mx-2;
            }
        }

        
    }

    // transition
    .change-all-enter-active .change-all-leave-active {
        transition: all .5s ease;
    }
    .change-all-leave-active {
       transition: all .5s ease;
    }
    .change-all-enter, .change-all-leave-to {
        opacity: 0;
    }
</style>